.modal {
  height: 100vh;
  left: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 10;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  -webkit-transition: background-color .5s ease .2s, -webkit-transform .5s ease;
  transition: background-color .5s ease .2s, -webkit-transform .5s ease;
  transition: transform .5s ease, background-color .5s ease .2s;
  transition: transform .5s ease, background-color .5s ease .2s, -webkit-transform .5s ease;
  display: flex;
  align-items: center; }
  .modal__content {
    background-color: #ffffff;
    margin: 0 auto;
    position: relative;
    width: 60%;
    padding: 16px; }
  .modal__close {
    align-items: center;
    background-color: #3F4448;
    border-radius: 50%;
    color: #ffffff;
    cursor: pointer;
    display: flex;
    justify-content: center;
    position: absolute;
    right: -8px;
    top: -8px;
    width: 32px;
    height: 32px; }
  .modal--show {
    background-color: rgba(36, 41, 46, 0.5);
    -webkit-transform: translate(0);
            transform: translate(0); }

